import React, {Component} from 'react'
import {connect} from 'react-redux'
// import {bindActionCreators} from 'redux'
import {addCharacterByID} from '../actions'
class CharactersList extends Component {
    render(){
        console.log('CharacterList.props', this.props);
        // const a = ['a','b','c'];
        // const b = a.map(e=>{
        //     return '<div>'+e+'</div>'
        // }) //wraps each element into a div
        return (
            <div>
                <h3>Characters List</h3>
                <ul className="list">
                    {
                        this.props.characters.map(c=>{
                            return (
                                <li key={c.id}
                                onClick={()=>
                                this.props.addCharacterByID(c.id)
                                }
                                className="list-item">
                                    <span>{c.name}</span> <i>+</i>
                                </li>
                            )
                        })
                    }
                </ul>
            </div>
        )
    }
}
function mapStateToProps(state){
    console.log('state', state)
    // return {}
    return {
        characters: state.characters
    }
}

// function mapDispatchToProps(dispatch){
//     return bindActionCreators({addCharacterByID}, dispatch)
// }
// export default connect(mapStateToProps, mapDispatchToProps)(CharactersList)
export default connect(mapStateToProps, {addCharacterByID})(CharactersList)