import React, { Component } from 'react';
import {connect} from 'react-redux'

class Stats extends Component {
    strength(){
        let strength = 0;
        this.props.heroes.forEach(hero=>strength+=hero.strength)
        return strength;
    }
    intelligence(){
        let intelligence = 0;
        this.props.heroes.forEach(hero=>intelligence+=hero.intelligence)
        return intelligence;
    }
    speed(){
        let speed = 0;
        this.props.heroes.forEach(hero=>speed+=hero.speed)
        return speed;
    }
  render() {
    return (
      <ul>
          <li>Strength: <i>{this.strength()}</i></li>
          <li>Intelligence: <i>{this.intelligence()}</i></li>
          <li>Speed: <i>{this.speed()}</i></li>
      </ul>
    );
  }
}
function mapStateToProps(state){
    return {
        heroes: state.heroes
    }
}
// export default Stats;
export default connect(mapStateToProps)(Stats)